<template>
	<view class="login-container">
		<view class="logo-section">
			<image class="logo" src="/static/logo.png" mode="aspectFit"></image>
			<text class="app-name">视消云</text>
		</view>

		<view class="form-section">
			<view class="input-item">
				<input
					class="input"
					type="number"
					v-model="phone"
					placeholder="请输入手机号"
					maxlength="11"
				/>
			</view>

			<view class="input-item">
				<input
					class="input"
					:type="showPassword ? 'text' : 'password'"
					v-model="password"
					placeholder="请输入密码"
				/>
				<image
					class="eye-icon"
					:src="showPassword ? '/static/icons/eye-open.png' : '/static/icons/eye-close.png'"
					@click="showPassword = !showPassword"
				></image>
			</view>

			<view class="forgot-password" @click="goToForgotPassword">
				<text>忘记密码</text>
			</view>

			<button class="login-btn" @click="handleLogin">登录</button>

			<view class="register-link">
				<text class="text">还没有账号？</text>
				<text class="link" @click="goToRegister">去注册</text>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			phone: '',
			password: '',
			showPassword: false
		}
	},
	methods: {
		handleLogin() {
			if (!this.phone) {
				uni.showToast({ title: '请输入手机号', icon: 'none' })
				return
			}
			if (!this.password) {
				uni.showToast({ title: '请输入密码', icon: 'none' })
				return
			}
			// TODO: 调用登录接口
			console.log('登录', this.phone, this.password)
		},
		goToForgotPassword() {
			uni.navigateTo({ url: '/pages/forgot-password/forgot-password' })
		},
		goToRegister() {
			uni.navigateTo({ url: '/pages/register/register' })
		}
	}
}
</script>

